import * as THREE from 'three';
import { Solution } from './';

import { PARAM } from '../../../../App';

import { getJSONFromURL, } from 'njscore';
export class DataTwitter {
    solution: Solution;
    active: boolean = false;
    dataRaw: any;
    mesh: THREE.Points;
    t = 0.0;

    dataScale: number = 1;
    colorScale: number = 1;

    pos: Float32Array;

    color: string = '#0ff';

    material: THREE.PointsMaterial;

    constructor(solution: Solution) {
        this.solution = solution;

        const fileName = 'real_data.json'
        const path = '/static/research/slow-zone/';

        let thePath = 'https://raw.githubusercontent.com/NamjuLee/data/master/Paris/twitter_data_2010-2015.json'; // process.env.PUBLIC_URL + path + fileName

        if (PARAM.url) {
            thePath = PARAM.url;
        }

        getJSONFromURL(thePath).then((data) => {
            this.dataRaw = data;

            this.initMesh('noNeighbsLog');
            this.solution.updateParam();
        });

    }
    initMesh(key: string) {
        const positions = [];
        const colors = [];


        const domain = this.dataRaw['meta']['domain_' + key]
        // console.log(domain)

        // const selYear = domain[0]
        // lat : 48.819493
        // lon : 2.3554159
        // noNeighbsLog : 0
        // noNeighbsLogDensity : 0
        // noNeighbsRaw : 0
        // noNeighbsWins : 0
        // noTwtsLog : 0
        // noTwtsLogDensity : 0
        // noTwtsRaw : 0
        // noTwtsWins : 0
        // noUsersLog : 0
        // noUsersLogDensity : 0
        // noUsersRaw : 0
        // noUsersWins : 0
        // treatmentCategory : 0
        // year : 2010

        for (let i = 0; i < this.dataRaw['cells'].length; ++i) {
            const cell = this.dataRaw['cells'][i];
            const [x, y] = this.solution.projectionToMercator(+cell['lon'], +cell['lat'])

            let keyIndex = +cell[key]
            keyIndex /= 2;

            const color = [keyIndex, keyIndex, 0];

            positions.push(x, keyIndex * this.dataScale, y)

            colors.push(color[0], color[1], color[2]);

        }
        // this.meshStreetOSM = this.LineSeg(this.streetOSM, [0, 1, 1]);

        const geometry = new THREE.BufferGeometry();
        const material = new THREE.PointsMaterial({ vertexColors: true, size: 0.5 });
        this.material = material;

        this.pos = new Float32Array(positions)

        geometry.setAttribute('position', new THREE.Float32BufferAttribute(this.pos, 3));
        geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
        geometry.computeBoundingSphere();

        this.mesh = new THREE.Points(geometry, material);
        this.solution.scene.add(this.mesh);

        //     console.log('done');
        this.active = true;

    }
    updateColor(r: number, g: number, b: number) {
        this.material.color.setRGB(r, g, b);
    }
    updateMesh(key: string) {
        this.active = false;
        this.removeMesh();
        this.initMesh(key);
    }
    removeMesh() {
        this.solution.scene.remove(this.mesh);
    }
    update() {
        if (!this.active) { return; }
        this.t += 0.05;
        // if (this.solution.ui.isActive_twitter) {
        this.updateAnimation();
    }
    updateAnimation() {

        let numVertices = this.mesh.geometry.attributes.position.count;
        for (let i = 0; i < numVertices; i++) {
            let o = this.mesh.geometry.attributes.position;

            let x = o.getX(i);
            // let y = o.getY(i);
            let z = o.getZ(i)

            let y = this.pos[(i * 3) + 1] * this.dataScale;

            y = y * (1 + Math.sin(x + y + this.t) * Math.cos(x + y + this.t))

            y = y < 0.1 ? 0.1 : y;
            // console.log(y)

            let p = new THREE.Vector3(x, y, z);

            this.mesh.geometry.attributes.position.setXYZ(i, p.x, p.y, p.z);

            // if (intersection !== null) {
            //     y = distanceTo(x, y, z, intersection.point.x, intersection.point.y, intersection.point.z) / 30
            // }


            const valNorm = y / this.colorScale;
            this.mesh.geometry.attributes.color.setXYZ(i, valNorm, 1 - valNorm, 0);
            // console.log(y)
        }

        // set to true each time you modify the positions:
        this.mesh.geometry.attributes.position.needsUpdate = true;
        this.mesh.geometry.attributes.color.needsUpdate = true;
        this.mesh.geometry.computeVertexNormals();

    }
}