import { NVector3, GLShaderCommon } from '../../../../../lib/NJSCore';

// import { Mat3 } from '../../AppCore/GLUtility/Mat3';

export class FilterGLViewDarkForDarken {

    gl: WebGLRenderingContext;
    program: WebGLProgram;
    fragmentShader: WebGLShader;
    vertexShader: WebGLShader;
    matrixLocation: WebGLProgram;
    posBuffer: WebGLBuffer;
    colBuffer: WebGLBuffer;
    vertexs: Float32Array;

    posLocAtt: number;
    colLocAtt: number;
    matLoc: WebGLUniformLocation;
    colLoc: WebGLUniformLocation;
    cVecLoc: WebGLUniformLocation;
    mouseLoc: WebGLUniformLocation;
    timeGL: WebGLUniformLocation;
    // startTime: number;
    // randomSeed: number;

    vShader: WebGLShader;
    fShader: WebGLShader;

    v = `
    attribute vec2 a_position;
    attribute vec4 a_color;
    
    varying vec4 f_color;

    void main() {

        vec2 mouse_distance = vec2(0,0) - (a_position.xy /vec2(2,2));
        float red = 1.0 - length(mouse_distance);
        f_color = vec4(a_position, 0, 1.0);
    
        // float dis = distance(vec2(a_position.xy), vec2(0,0));

        // f_color = vec4(dis * 0.6, 0,0,1);
        gl_Position = vec4( a_position, 0, 1);
    }
    `;

    f = `
    precision highp float;

    varying vec4 f_color;
    
    void main() {
        vec2 mouse_distance = vec2(0,0) - (f_color.xy /vec2(5,5));

        float col =  length(mouse_distance*5.);
        float val = 1.0 - length(mouse_distance*8.);
        
        gl_FragColor = vec4(0.19,0.19,0.19, col);

    }
    `;

    positionAttribLocation: WebGLProgram;
    colorAttribLocation: WebGLProgram;
    translation: NVector3;
    color: number[];
    colArray: Float32Array;

    constructor(gl: WebGLRenderingContext, xt: number = 100, yt: number = 100, zt: number = 0, xs: number = 1, ys: number = 1, zs: number = 1) {
        this.gl = gl;
        // this.translation = new NVector3(xt, yt, zt);
        // this.angleInRadians = 0;
        // this.scale = new NVector3(xs, ys, zs);
        this.color = [Math.random(), Math.random(), Math.random(), 1];
        this.InitShader();
    }
    InitShader() {
        // let vShader = GLUtility.CreateShaderFromPath(this.gl, this.gl.VERTEX_SHADER,'/shader/vsCanvas.glsl');
        // let fShader = GLUtility.CreateShaderFromPath(this.gl, this.gl.FRAGMENT_SHADER, '/shader/fsColor.glsl');

        let vShader = GLShaderCommon.CreateShader(this.gl, this.gl.VERTEX_SHADER, this.v); // '/shader/vsCanvas.glsl');
        let fShader = GLShaderCommon.CreateShader(this.gl, this.gl.FRAGMENT_SHADER, this.f); // '/shader/fsColor.glsl');

        if (vShader && fShader) {
            this.vShader = vShader;
            this.fShader = fShader;
            let program = GLShaderCommon.CreateProgram(this.gl, vShader, fShader);
            if (program) { this.program = program; }
        }

        // this.InitVerext();
    }
    Render(gl: WebGLRenderingContext) {

        let col = [0.1, 0, 0, 1];

        this.vertexs = new Float32Array([
            // left column
            -1, -1, 1, 1, 1, 1,
            - 1, 1, col[0], col[1], col[2], col[3],
            1, -1, col[0], col[1], col[2], col[3],

            -1, 1, 1, 1, 1, 1,
            1, -1, col[0], col[1], col[2], col[3],
            1, 1, col[0], col[1], col[2], col[3],

        ]);

        this.posBuffer = this.gl.createBuffer() as WebGLBuffer;
        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.posBuffer);
        this.gl.bufferData(this.gl.ARRAY_BUFFER, new Float32Array(this.vertexs), gl.STATIC_DRAW);

        this.posLocAtt = this.gl.getAttribLocation(this.program, 'a_position');
        // Tell the attribute how to get data out of posBuffer (ARRAY_BUFFER)
        let size = 2;          // 2 components per iteration
        let type = gl.FLOAT;   // the data is 32bit floats
        let normalize = false; // don't normalize the data
        let stride = 6 * Float32Array.BYTES_PER_ELEMENT; // 0 = move forward size * sizeof(type) each iteration to get the next position
        let offset = 0;        // start at the beginning of the buffer
        gl.vertexAttribPointer(this.posLocAtt, size, type, normalize, stride, offset);
        gl.enableVertexAttribArray(this.posLocAtt);

        // this.colLocAtt = this.gl.getAttribLocation(this.program, 'a_color');
        // size = 4;          // 2 components per iteration
        // type = gl.FLOAT;   // the data is 32bit floats
        // normalize = false; // don't normalize the data
        // stride = 6 * Float32Array.BYTES_PER_ELEMENT; // 0 = move forward size * sizeof(type) each iteration to get the next position
        // offset = 2 * Float32Array.BYTES_PER_ELEMENT;        // start at the beginning of the buffer
        // gl.vertexAttribPointer(this.colLocAtt, size, type, normalize, stride, offset);
        // gl.enableVertexAttribArray(this.colLocAtt);

        gl.useProgram(this.program);
        let primitiveType = gl.TRIANGLES; // LINE_LOOP; // gl.TRIANGLE_STRIP;
        offset = 0;
        let count = 6; // this.vertexs.length * 0.5;
        gl.drawArrays(primitiveType, offset, count);

    }
}