import { Solution } from '../';;
export class UI {
    solution: Solution;
    hostDiv: HTMLElement;

    isActiveMainStreet = true;
    isActive_streetOSM = false;
    isActive_treated_st = false;
    isActive_control_st = false;
    isActive_zones_30 = false;
    isActive_zones_buffer = false;
    isActive_twitter = false;

    year: number = 2010;

    divCameraPos: HTMLElement;

    constructor(solution: Solution) {
        this.solution = solution;
        this.hostDiv = solution.hostDiv;

        // console.log(1);
        if (this.solution.PARAM.local_ui !== undefined) {
            if((this.solution.PARAM.local_ui as boolean) === true) {
                this.appendUI();
            }
        } else {
            this.appendUI();
        }
        
    }
    public appendUI() {

        const mainMenu = document.createElement('div');
        mainMenu.style.position = 'absolute';
        mainMenu.style.margin = '5px';
        this.hostDiv.append(mainMenu);


        
        const divCameraPos = document.createElement('dv');
        divCameraPos.style.display = 'flex';
        divCameraPos.style.marginBottom = '10px';
        divCameraPos.textContent = 'pos target';
        this.divCameraPos = divCameraPos;
        mainMenu.append(divCameraPos);

        // let checkBoxUIA = getCheckBox('high_low_connectedness_user');
        // mainMenu.append(checkBoxUIA.div);
        // checkBoxUIA.checkBox.onchange = () => {
        //     if (checkBoxUIA.checkBox.checked === true){
        //         this.isActive_high_low_connectedness_user = true;

        //     } else {
        //         this.isActive_high_low_connectedness_user = false;
        //     }
        // }
        const divInputColor = document.createElement('input');
        mainMenu.append(divInputColor)
        divInputColor.type = 'color';
        divInputColor.oninput = () => {
            this.solution.drawing.updateColor(divInputColor.value);
            // console.log(divInputColor.value)
            // if (this.capture) {
            //     const colArray = hex2RGBA(divInputColor.value)
            //     // this.capture.opacity = +sl3.slider.value * 0.01;
            //     this.capture.rgb = colArray;
            // //     console.log(colArray)
            // }
        }

        let checkBoxUIB = getCheckBox('main street');
        mainMenu.append(checkBoxUIB.div);
        checkBoxUIB.checkBox.checked = true;
        checkBoxUIB.checkBox.onchange = () => {
            if (checkBoxUIB.checkBox.checked === true) {
                this.isActiveMainStreet = true;
            } else {
                this.isActiveMainStreet = false;
            }
        }


        let checkBoxUI_OSM = getCheckBox('OSM street');
        mainMenu.append(checkBoxUI_OSM.div);
        checkBoxUI_OSM.checkBox.onchange = () => {
            if (checkBoxUI_OSM.checkBox.checked === true) {
                this.isActive_streetOSM = true;
            } else {
                this.isActive_streetOSM = false;
            }
        }


        let checkBoxUIC = getCheckBox('treated street');
        mainMenu.append(checkBoxUIC.div);
        checkBoxUIC.checkBox.onchange = () => {
            if (checkBoxUIC.checkBox.checked === true) {
                this.isActive_treated_st = true;
            } else {
                this.isActive_treated_st = false;
            }
        }


        let checkBoxUID = getCheckBox('control street');
        mainMenu.append(checkBoxUID.div);
        checkBoxUID.checkBox.onchange = () => {
            if (checkBoxUID.checkBox.checked === true) {
                this.isActive_control_st = true;
            } else {
                this.isActive_control_st = false;
            }
        }
        let checkBoxUIE = getCheckBox('zones 30');
        mainMenu.append(checkBoxUIE.div);
        checkBoxUIE.checkBox.onchange = () => {
            if (checkBoxUIE.checkBox.checked === true) {
                this.isActive_zones_30 = true;
            } else {
                this.isActive_zones_30 = false;
            }
        }


        let slider = getSlider('Year')
        slider.div.style.marginTop = '25px';
        slider.text.textContent = 'Year: 2019'
        mainMenu.append(slider.div);

        slider.slider.oninput = () => {
            this.year = +slider.slider.value;
            slider.text.textContent = 'Year: ' + slider.slider.value;
            this.solution.drawing.updateYear(+slider.slider.value);
        }


        //  ..............................

        let checkBoxUI_Node = getCheckBox('twitter data');
        checkBoxUI_Node.div.style.marginTop = '25px';
        mainMenu.append(checkBoxUI_Node.div);
        checkBoxUI_Node.checkBox.onchange = () => {
            if (checkBoxUI_Node.checkBox.checked === true) {
                this.isActive_twitter = true;
            } else {
                this.isActive_twitter = false;
            }
        }
        let sliderDataScale = getSlider('Scale', '1', '10');
        sliderDataScale.text.textContent = 'Scale: 1'
        mainMenu.append(sliderDataScale.div);
        sliderDataScale.slider.defaultValue = '1';
        sliderDataScale.slider.oninput = () => {
            this.year = +sliderDataScale.slider.value;
            sliderDataScale.text.textContent = 'Scale: ' + sliderDataScale.slider.value;
            this.solution.dataTwitter.dataScale = +sliderDataScale.slider.value;
        }
        let sliderDataScaleColor = getSlider('Color', '1.', '50.');
        sliderDataScaleColor.text.textContent = 'Color: 1'
        mainMenu.append(sliderDataScaleColor.div);
        sliderDataScaleColor.slider.defaultValue = '1';
        sliderDataScaleColor.slider.oninput = () => {
            this.year = +sliderDataScaleColor.slider.value;
            sliderDataScaleColor.text.textContent = 'Color: ' + sliderDataScaleColor.slider.value;
            this.solution.dataTwitter.colorScale = +sliderDataScaleColor.slider.value;
        }

        let checkBoxAni = getCheckBox('Animation');
        checkBoxAni.checkBox.defaultChecked = true;
        mainMenu.append(checkBoxAni.div);
        checkBoxAni.checkBox.onchange = () => {
            if (checkBoxAni.checkBox.checked === true) {
                this.solution.dataTwitter.active = true;
            } else {
                this.solution.dataTwitter.active = false;
            }
            this.solution.dataTwitter.updateAnimation();
        }

        const theField = {
            noNeighbsLog: 'noNeighbsLog',
            noNeighbsLogDensity: 'noNeighbsLogDensity',
            noNeighbsRaw: 'noNeighbsRaw',
            noNeighbsWins: 'noNeighbsWins',
            noTwtsLog: 'noTwtsLog',
            noTwtsLogDensity: 'noTwtsLogDensity',
            noTwtsRaw: 'noTwtsRaw',
            noTwtsWins: 'noTwtsWins',
            noUsersLog: 'noUsersLog',
            noUsersLogDensity: 'noUsersLogDensity',
            noUsersRaw: 'noUsersRaw',
            noUsersWins: 'noUsersWins',
            treatmentCategory: 'treatmentCategory',
        }

        const optionList = document.createElement('select');
        mainMenu.append(optionList);
        for (let i in theField) {
            const option = document.createElement('option')
            option.value = i;
            option.textContent = i
            optionList.appendChild(option)
        }

        optionList.onchange = (e) => {
        //     console.log((e.target as any).value)
            this.solution.dataTwitter.updateMesh((e.target as any).value);
        };

    }
}


const getCheckBox = (title: string) => {
    const div = document.createElement('div');
    div.style.display = 'flex';

    const checkBox = document.createElement('input');
    checkBox.type = 'checkbox';
    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(checkBox);
    div.appendChild(text);

    return {
        div, checkBox
    }
};

const getSlider = (title: string, min: string = '2009', max: string = '2015') => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = min;
    slider.max = max;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider, text
    }
};